.floating-wrap{
 
 width: 70px;
 height: 70px;
    color:white;
    font-weight: bold;
    border-radius: 50%;
    border: 1px solid #fff ;
    position: fixed;
    bottom: 3rem;
    right: 3rem;
    font-size:50px;
   text-align: center;
    /* background-color: #ff6363;  */
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 14px 18px 46px -22px rgb(37, 29, 58, 0.55);
    cursor: pointer;
}
.floating-wrap:hover{

    /* background-color: #e04d01; */
    background-color: #ff6363;
     
}
.floating-wrap:hover #floatBtn{color: white !important;}