@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap');

.card-wrap{
    width: 300px;
    height: 150px;
    padding: 2rem;
    margin: 0.5rem  1rem;
    color: #fff;
    font-family: ubuntu;
    background-color: rgb(255, 159, 159);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(4px);
    --webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
}
.card-e-icon{
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    font-size: 1.2rem;
}
.btn-save{
    margin-top: 5px;
    width: 65px;
    height: 25px;
    background-color:rgb(70, 88, 255) ;
    color: rgb(255, 255, 255);
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
    border: 2px solid rgb(70, 88, 255);
  

}
.btn-save:hover{
   background-color: aliceblue;
   color:rgb(70, 88, 255)
}
input{
    outline: none;
    background-color: transparent;
    border-width: 0px 0px 3px 0px;
    border-color:#EEEEEE;
    margin: 10px 0px;
    font-size: 1.3rem; 
    margin: 4px;
    color: #EEEEEE;
}